import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams, ViewController } from 'ionic-angular';
import { ExamTypeMapper } from '../../../models/exam-type.model';
import { ExamTypesProvider } from '../../../providers/exam-types/exam-types';

/**
 * Generated class for the ExamSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'ExamSearchPage'
})
@Component({
  selector: 'page-exam-search',
  templateUrl: 'exam-search.html',
})
export class ExamSearchPage {
  searching: boolean = false;

  examName: string;

  currentItems: ExamTypeMapper[] = [];

  examSelected: ExamTypeMapper[] = [];

  page: number = 0;

  q: string = '';

  clinic: any = {};

  type: string = 'lab';

  constructor(public navCtrl: NavController, private viewCtrl: ViewController,  public navParams: NavParams,
              public examTypesPrv: ExamTypesProvider,
              public modalCtrl: ModalController) {
    this.examSelected = this.navParams.get('examSelected') ? this.navParams.get('examSelected') : this.examSelected;
    this.clinic = this.navParams.get('clinic') ? this.navParams.get('clinic') : this.clinic;
  }

  ionViewDidLoad() {
    let params = {page: this.page, q: this.q, type: this.type, clinic_id: null};
    this.getItems(params);
    if (this.clinic && this.clinic.id) {
      params.clinic_id = this.clinic.id;
      // this.getSelectedItems(params);
    }
  }

  getItems(params?: any) {
    return new Promise((resolve) => {
      this.examTypesPrv.query(params).subscribe((data: ExamTypeMapper[]) => {
        if (params && params.q) {
          this.currentItems = data;
          resolve();
          return;
        }
        this.currentItems = this.currentItems.concat(data);
        resolve();
      });
    });
  }

  getSelectedItems(params?: any) {
    return new Promise((resolve) => {
      this.examTypesPrv.query(params).subscribe((data: ExamTypeMapper[]) => {
        this.examSelected = data;
        resolve();
      });
    });
  }

  onCancel() {
    this.page = 0;
    this.q = '';
    this.getItems();
  }

  getExams(ev: any) {
    const val = ev.target.value;

    if (val === '') {
      this.q = '';
      this.page = 0;
      this.getItems({page: this.page, q: this.q});
      return;
    }

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '' && val.length > 3) {
      this.q = val;
      this.page = 0;
      this.getItems({page: this.page, q: this.q});
    }
  }

  dismiss() {
    this.viewCtrl.dismiss({ examSelected: this.examSelected });
  }

  addExam(exam: ExamTypeMapper) {

    if (exam.clinics) {
      delete exam.clinics;
    }

    if (exam.checked) {
      this.examSelected.push(exam);
      return;
    }

    this.examSelected.forEach((value: ExamTypeMapper, index) => {
      if (exam.id === value.id) {
        this.removeExamSelected(index);
        return;
      }
    });
  }

  removeExamSelected(index: number) {
    let exam = this.examSelected[index];
    exam.checked = false;
    this.examSelected.splice(index, 1);
  }

  doInfinite(infiniteScroll) {
    return new Promise((resolve) => {
      console.log('doInfinite');
      this.page++;

      this.getItems({page: this.page}).then(() => {
        resolve();
        infiniteScroll.complete();
      });
    });
  }

  filterByExamType($event) {
    this.currentItems = [];
    this.getItems({page: this.page, q: this.q, type: this.type});
  }
}



